import * as React from 'react';
import { render } from 'react-dom';
import ReactGlobe from 'react-globe';

import markers from './markers';
import markerRenderer from './markerRenderer';

import './styles.css';

function onClickMarker(marker, markerObject, event) {
  window.open('https://opensea.io/assets/0x5c1110907a0f0d39b7e3b7bf472981bc19e88a62/' + marker.id , '_blank')
}

function App() {
  return (
    <div className="App">
      <ReactGlobe
        onClickMarker={onClickMarker}
        // onMouseOverMarker={onMouseOverMarker}
        globeOptions={{
          texture:
            'https://raw.githubusercontent.com/chrisrzhou/react-globe/master/textures/globe_dark.jpg',
            cloudsOpacity: 0.05,
            enableClouds: true,
            glowCoefficient: 0.15,
            glowColor: '#0264ed',
            glowPower: 5,
            glowRadiusScale: 0.25,
            enableGlow: true,
        }}
        cameraOptions={{
          autoRotateSpeed: 0.1,
          enableAutoRotate: true,
          enableRotate: true,
          rotateSpeed: 0.3,
        }}
        lightOptions={{
          ambientLightColor: '#33a8cc',
          ambientLightIntensity: 1.5,
          pointLightColor: '#7702ed',
          pointLightIntensity: 3,
          pointLightPositionRadiusScales: [2, 1, -1],
        }}
        markers={markers}
        // markerOptions={{ renderer: markerRenderer }}
        markerOptions={{
          activeScale: 1.1,
          enableTooltip: true,
          enterAnimationDuration: 3000,
          enterEasingFunction: ['Bounce', 'InOut'],
          exitAnimationDuration: 3000,
          exitEasingFunction: ['Cubic', 'Out'],
          getTooltipContent: marker => `${marker.city} ( LAND ID: ${marker.id} )`,
          radiusScaleRange: [0.001, 0.02],
        }}
      />
    </div>
  );
}

const rootElement = document.getElementById('root');
render(<App />, rootElement);
